@charset "UTF-8";
@import url(https://fonts.googleapis.com/css?family=Montserrat:400,700);
@import url(https://fonts.googleapis.com/css?family=Fjalla+One);
.badgee {
  position: relative;
  width: 216px;
  display: inline-block;
  margin: 40px; }
  .badgee .title {
    font-family: "Montserrat", sans-serif;
    font-weight: bold;
    font-size: 1.7em;
    position: absolute;
    top: -28px;
    border-radius: 8px 8px 0 0;
    text-align: center;
    width: 100%;
    background: red;
    padding: 20px 0; }
  .badgee .small-btn {
    width: 50px;
    height: 25px; }
  .badgee .a {
    color: white; }
  .badgee .subtitle {
    background-color: transparent;
    position: absolute;
    font-family: "Fjalla One", sans-serif;
    font-size: 1.8em;
    -webkit-border-radius: 15px;
    border: transparent;
    text-align: center;
    color: white;
    position: absolute;
    top: 100px;
    left: 80px; }
    .badgee .subtitle:after {
      content: "  ";
      display: block;
      font-size: .4em;
      position: relative;
      margin: 15px 0 0;
      transition: all .3s; }
  .badgee:hover .subtitle:after {
    word-spacing: 30px; }

.red .title {
  background: #DC514E; }

.red .subtitle:after {
  color: #DC514E; }

.green .title {
  background: #2ecc71; }

.green .subtitle:after {
  color: #2ecc71; }

.gray .title {
  background: #95a5a6; }

.gray .subtitle:after {
  color: #95a5a6; }

.yellow .title {
  background: #f1c40f; }

.yellow .subtitle:after {
  color: #f1c40f; }

.blue .title {
  background: #3498db; }

.blue .subtitle:after {
  color: #3498db; }
